<template>
     <div class="container">
        <div class="main__info" v-if="todos">
            <div class="main__info-item" v-for="todo in todos" :key="todo.id">
                <h2>{{ todo.id }}</h2>
                <p>{{ todo.title }}</p>
                <p>{{ todo.completed }}</p>  
            </div>
        </div>
        <div v-else>
            Loading....
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                todos: null
            }
        },
        created() {
            this.getTodos()
        },
        methods: {
           async getTodos() {
                let res = await fetch('https://jsonplaceholder.typicode.com/todos')
                let data = await res.json()
                this.todos = data
            }
        },
    }
</script>

